import React, { useEffect, useState } from 'react';
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss';

let 박스 = styled.div`
  padding : 20px;
`;

let 제목 = styled.h4`
  font-size: 25px;
`;

function Detail(props) {

  let [alert, setAlert] = useState(true);
  let [inputData, setInputData] = useState()
  
  useEffect(() => {
    let timer = setTimeout(()=>{ setAlert(false) }, 2000);
    console.log("하이?");
    return () => { clearTimeout(timer) }  // <Detail>이 사라질때 실행할 코드
  },[alert]);  //alert이 변경될때만 실행
  

  let { id } = useParams();
  let history = useHistory();  // 방문기록 저장
  let product = props.shoes.find(data => data.id == id);

  return (
    <div className="container">
      <박스>
        <제목 className="red">Detail</제목>
      </박스>
 
      {/* <input onChange={(e)=>{ setInputData(e.target.value) }} /> */}
      { 
        alert === true 
        ? (<div className="my_alert2">
            <p>재고가 얼마 남지 않았습니다.</p>
          </div>)
        : null 
      }
      <div className="row">
        <div className="col-md-6">
          <img src="https://codingapple1.github.io/shop/shoes1.jpg" width="100%" />
        </div>
        <div className="col-md-6 mt-4">
          <h4 className="pt-5">{product.title}</h4>
          <p>{product.content}</p>
          <p>{product.price}</p>

          <Info stock={props.stock}></Info>

          <button className="btn btn-danger" onClick={ ()=>{ props.setStock([9,11,12]) }}>주문하기</button> 
          <button className="btn btn-danger" onClick={ ()=>{ history.goBack(); }}>뒤로가기</button> 
        </div>
      </div>
    </div> 
  )
}

function Info(props) {
  return (
    <p>재고: {props.stock[0]} </p>
  )
}

export default Detail;